<script>


export default {
  name: "home",
  components: {
  },
  data() {
    return {
      poolMoney: 0,
      poolAwardMoney: 0,
      intervalTimer:null,
      isFirstRun:true,
    };
  },
  computed: {

    pcfxContract: function () {
      return this.$store.state.web3.pcfxContract;
    },
    ticketContract: function () {
      return this.$store.state.web3.ticketContract;
    },
    poolGoContract: function () {
      return this.$store.state.web3.poolGoContract;
    },
    prizePoolContract: function () {
      return this.$store.state.web3.prizePoolContract;
    },
    periodicPrizeStrategyContract: function () {
      return this.$store.state.web3.periodicPrizeStrategyContract;
    },
    tokenFaucetContract: function () {
      return this.$store.state.web3.tokenFaucetContract;
    },
    cfxStakeContract: function () {
      return this.$store.state.web3.cfxStakeContract;
    },
    cfxUniswapV2PairContract: function () {
      return this.$store.state.web3.cfxUniswapV2PairContract;
    },

  },
  created() {
  },

  async mounted() {
    var that = this;

    that.update();
    that.intervalTimer = setInterval(that.update, 6000)
  },
  beforeDestroy() {
    var that = this;
    clearTimeout(that.intervalTimer);
  },
  methods: {
    showTokenMorePage(){
      window.open("https://docs.poolgo.finance/poolgo-finance/tokens");
    },
    showIntroduceMorePage(){
      window.open("https://docs.poolgo.finance/poolgo-finance/");
    },
    showRiskMorePage(){
      window.open("https://docs.poolgo.finance/poolgo-finance/risks");
    },
    showIns3Page(){
      window.open("https://www.ins3.finance/#/");
    },
    showConfluxPage(){
      window.open("https://confluxnetwork.org/");
    },
    showTrustPage(){
      window.open("https://trustdomains.org/#/");
    },
    showLinkDnsPage(){
      window.open("https://www.linkdns.com/");
    },
    showTwitterPage(){
      window.open("https://twitter.com/PoolGoDefi");
    },
    showDiscordPage(){
      window.open("https://discord.gg/x4WWrDd2tJ");
    },
    showGithubPage(){
      window.open("https://github.com/poolgo");
    },
    showMediumPage(){
      window.open("https://poolgo.medium.com/");
    },
    showTelgramPage(){
      window.open("https://t.me/poolgofinance");
    },
    showPoolsPage(){
      this.$router.push({
        path: "/pools"
      });
    },
    async update() {
      var that = this;

      // console.log(price.toFormat(1));
      let totalFunds = await that.cfxStakeContract.balanceOfUnderlyingView();
      // console.log("totalFunds" + totalFunds)
      let tickets = await that.prizePoolContract.accountedBalance();

      that.prizePeriodRemainingSeconds = (await that.periodicPrizeStrategyContract.prizePeriodRemainingSeconds())
      let interest = totalFunds.multipliedBy(that.prizePeriodRemainingSeconds.multipliedBy(0.0408).div(31536000));
      // console.log("tickets" + tickets)
      var award = totalFunds.plus(interest).minus(tickets);
      // console.log("totalFunds")
      // console.log(totalFunds.toFixed(3))
      // console.log(that.prizePeriodRemainingSeconds.toNumber())
      // console.log(interest.toFixed(3))
      // console.log(award.toFixed(3))

      let poolAwardMoney = award.multipliedBy(that.$store.state.web3.pcfxPrice);

      let poolBalance = tickets;
      let poolMoney = poolBalance.multipliedBy(that.$store.state.web3.pcfxPrice);
      that.poolAwardMoney = poolAwardMoney.toFormat(0);
      that.poolMoney = poolMoney.toFormat(0);

    },
  },
};
</script>

